@import './colors.scss';


:root{
    font-family: Georgia, 'Times New Roman', Times, serif;
}

#content{
    width: 100%;
    height: 100%;
}

.hide{
    display: none;
}

.show{
    display: block;
}

#body{
    overflow: hidden;
}

#pills-tab {
    margin-bottom: 15px;
}

.nav-pills .nav-link.active, .nav-pills .show > .nav-link {
    color: #fff;
    background-color: $primary_color;
}

.nav-pills .nav-link {
    color: $primary_color;
}